@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap");
.page-title {
  font-size: 2rem;
  color: rgba(35, 108, 243, 0.533);
  padding-bottom: 1rem;
  font-family: "Roboto Slab", serif;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.card-container {
  display: flex;
  width: 10rem;
  background-color: #FBE092;
  flex-direction: column;
  text-align: left;
  padding: 1rem;
  border: none;
  border-radius: 3px;
  margin: 4rem;
  margin-right: 0;
}
@media (min-width: 1020px) {
  .card-container {
    width: 41.6666666667%;
  }
}

.card-text-container {
  padding-bottom: 6rem;
  margin-left: 2rem;
  margin-right: 2rem;
}

.card-title {
  font-size: 2.5rem;
  color: white;
  padding-bottom: 1rem;
  font-family: "Roboto Slab", serif;
}

.card-sub-title {
  color: white;
  font-family: "Roboto Slab", serif;
}

.card-text {
  color: white;
  font-family: "Quicksand", sans-serif;
}

.button-inline-container {
  display: flex;
  display: inline-block;
  padding: 1rem;
}

.form-title {
  color: #333;
  font-family: #6458F5;
  font-size: x-large;
  font-weight: bold;
}

header {
  height: 100px;
  background-color: #FBE092;
  display: flex;
  justify-content: space-between;
}

.nav {
  width: auto;
}

.nav-link {
  display: none;
  font-size: 1.5rem;
  font-family: "Quicksand", sans-serif;
  color: #6458F5;
  text-decoration: none;
}
.nav-link:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .nav-link {
    display: block;
    padding-left: 1rem;
    font-size: 1rem;
  }
}

.hamburger {
  cursor: pointer;
  color: #fff;
  font-size: 2.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 1rem;
}
.hamburger:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.close-btn {
  cursor: pointer;
  color: #fff;
  font-size: 3rem;
  position: absolute;
  top: 22px;
  right: 12px;
}
.close-btn:hover {
  color: #6458F5;
}
@media (min-width: 768px) {
  .close-btn {
    display: none;
  }
}

.nav-menu {
  height: 100%;
  display: flex;
  background-color: #FBE092;
}
@media (min-width: 768px) {
  .nav-menu {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    padding-right: 3rem;
  }
}

.nav-menu-toggle {
  background-color: #FBE092;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 50vh;
  z-index: 100;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
  position: relative;
  left: -241px;
  top: 0;
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.nav-menu-toggle .nav-link {
  display: block;
}
@media (min-width: 768px) {
  .nav-menu-toggle {
    display: none;
  }
}

.container {
  position: relative;
  max-width: 100%;
  padding: 0 3rem;
}

.flex-container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex-container {
    flex-direction: row;
  }
}

.flex-column {
  flex-direction: column;
}
.flex-column-reverse {
  flex-direction: column-reverse;
}

.flex-row {
  flex-direction: row;
}
.flex-row-reverse {
  flex-direction: row-reverse;
}

.item {
  order: 0;
  font-size: 1rem;
  max-width: 100%;
  margin: 4rem 0;
}
@media (min-width: 1020px) {
  .item {
    font-size: 2rem;
  }
}

.home-container {
  width: 100%;
}
@media (min-width: 768px) {
  .home-container {
    width: 50%;
    padding: 1rem;
  }
}

.column-9 {
  width: 100%;
}
@media (min-width: 768px) {
  .column-9 {
    width: 75%;
  }
}

.scroll {
  overflow-y: scroll;
  max-height: 500px;
}

.items-wrapper {
  margin: auto;
  padding-top: 50px;
}

.item-container {
  margin-top: 40px;
  padding: 0 50px;
  background-color: #FBE092;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .item-container {
    flex-direction: column;
    padding: 10px;
  }
}

.item-details {
  line-height: 2;
}

.item-img > img {
  width: 260px;
  height: 170px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
}

.button-purple {
  background-color: #6458F5;
  border: none;
  border-radius: 3px;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}